/**
 * Makes the Navigationheader in Singlesection smaller by scrolling
 */
export function scrollHeader() {
  const navigationHeader = document.getElementById("custom-top-nav");
  const innerNavigationHeader = document.querySelector(
    ".inner-title-navigation"
  );
  const progressBar = document.querySelector(".mooin4ection-div");

  var prevScrollpos = window.pageYOffset;
  window.onscroll = function () {
    var currentScrollPos = window.pageYOffset;
    if (prevScrollpos > currentScrollPos) {
      navigationHeader.style.height = "10rem";
      innerNavigationHeader.style.opacity = 1;
      progressBar.style.transform = "translateY(0rem)";
    } else if (prevScrollpos < currentScrollPos) {
      navigationHeader.style.height = "5rem";
      innerNavigationHeader.style.opacity = 0;
      progressBar.style.transform = "translateY(-3rem)";
    }
    prevScrollpos = currentScrollPos;
  };
}
